/* body {
  margin: 0;
} */
.topnav {
  overflow: hidden;
  background-color: black;
  position: fixed;
  width: 100%;
  z-index: 100;
}
.topnav button {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}
.icon{
 position: fixed;
 right: 0;
}
.topnav button:hover {
  background-color: #ddd;
  color: black;
}

.topnav button.active {
  /* background-color: #04AA6D; */
  color: white;
  
}

.topnav .icon {
  display: none;
}
.icon:hover{
 background-color: #04AA6D;
}

@media screen and (max-width: 600px) {
  .topnav button:not(:first-child) {display: none;}
  .topnav button.icon {
   position: relative;
    display: block;
  }
}


@media screen and (max-width: 600px) {
  /* .topnav.responsive {position: relative;} */
  .topnav.responsive .icon {
    /* position: absolute; */
    right: 0;
    top: 0;
  }
  .topnav.responsive button {
    float: none;
    display: block;
    width: 100%;
    text-align: left;
  }

}